import { Component } from '@angular/core';
import { NavController, AlertController } from 'ionic-angular';
import { PlayersPage } from '../players/players';
import { LoginPage } from '../login/login';
import { MembershipPage } from '../membership/membership';
import { TeamsPage } from '../teams/teams';
import { SponsorPage } from '../sponsor/sponsor';
import { SocialuploadPage } from '../socialupload/socialupload';

import { Connect } from '../../providers/connect';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

 public feed : any[];
 public banner : any = './assets/img/blankbanner.jpg';
 public myAc : any = '';
 public manager : any = false;
  constructor(public navCtrl: NavController, private connect : Connect, public alertCtrl: AlertController) {
    this.feed = [];
    this.manager = false;
  }

  ionViewDidEnter()  {
     this.myAc = window.localStorage.getItem('myac');
     if (!this.myAc) this.myAc='';
     let that=this;
        let url = 'zs_socialfeed.php?me='+that.myAc;
        this.connect.getList(url).subscribe(
         data => {
            that.feed = data.feed;
            that.banner = data.banner;      
            that.manager = data.manager;    
         },
         err => {
         }
        ); 
  }

  teams()
 { this.navCtrl.push(TeamsPage);
 }

 showSponsor() {
    this.navCtrl.push(SponsorPage);
 }

  showPlayers() {
    this.navCtrl.push(PlayersPage);
  }

  showUpload() {
    this.navCtrl.push(SocialuploadPage);
  }

  doLogout()  {
    this.myAc='';
    window.localStorage.removeItem('myac');
  } 

  login()  {
    this.navCtrl.push(LoginPage);
  } 

  membership() {
    if (this.myAc.length) this.navCtrl.push(MembershipPage); 
    else this.navCtrl.push(LoginPage);
  }

  signOutAlert() {
      let that=this;
      let alert = this.alertCtrl.create({
      title: 'Are you sure?',
      subTitle: '',
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
          }
        },
        {
          text: 'Yes',
          role: 'cancel',
          handler: () => {
            that.doLogout();
          }
        }
      ]
    });
    alert.present();
 }

 uptick(a)
 { if (!this.feed[a].liked) {

     this.feed[a].likes++;
     this.feed[a].liked=true;
     let that=this;
        let url = 'zs_uptick.php?id='+this.feed[a].id;
        this.connect.getList(url).subscribe(
         data => {
         },
         err => {
         }
        ); 
   }
 }

}




