import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController } from 'ionic-angular';
import { Camera, CameraOptions } from '@ionic-native/camera';
import { Connect } from '../../providers/connect';



@Component({
  selector: 'page-socialupload',
  templateUrl: 'socialupload.html',
})
export class SocialuploadPage {

  pictaken : any = false;
  su : any;

  constructor(public navCtrl: NavController, public navParams: NavParams, private camera: Camera, public connect: Connect, public loadingCtrl: LoadingController) {

     this.su = {
      title : '',
      content: '',
      picdata: ''
     }

  }

  ionViewDidLoad() {
     this.pictaken = false;
  }

  getpicture() {
 
   const options: CameraOptions = {
     quality: 70,
     targetWidth:600,
     targetHeight:300,
     destinationType: this.camera.DestinationType.DATA_URL,
     encodingType: this.camera.EncodingType.JPEG,
     mediaType: this.camera.MediaType.PICTURE
   }

 let that = this;

 this.camera.getPicture(options).then((imageData) => {
    that.pictaken = true;
    that.su.picdata = imageData;
 }, (err) => {
    alert(JSON.stringify(err));
 });

 }

  getcameraroll() {
   const options: CameraOptions = {
     quality: 70,
     targetWidth:600,
     targetHeight:300,
     sourceType: 0,
     destinationType: this.camera.DestinationType.DATA_URL,
     encodingType: this.camera.EncodingType.JPEG,
     mediaType: this.camera.MediaType.PICTURE
   }

 let that = this;

 this.camera.getPicture(options).then((imageData) => {
    that.pictaken = true;
    that.su.picdata = imageData;
 }, (err) => {
    alert(JSON.stringify(err));
 });

 }

 dosubmit() {

      let that = this;
       let loading = this.loadingCtrl.create({
         content: 'Please wait...'
       });
       loading.present();
       let url = 'zs_socialupload.php';
       let totdata = JSON.stringify(that.su);
       this.connect.PostQuery(url,totdata).subscribe(
          data => {
            loading.dismiss();
            that.navCtrl.pop();
          },
          err => {
            loading.dismiss();
            that.connect.logError(err)
          }
        );

 }

}



