import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Connect } from '../../providers/connect';

/**
 * Generated class for the ProfilePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-profile',
  templateUrl: 'profile.html',
})
export class ProfilePage {

  name : any = '';
  img : any = 'https://theparcelpeople.com.au/soccer/general.jpg';
  bio : any = '';
  awards : any = '';
  position: any = '';
  favplayer : any = '';
  favteam : any = '';
  dob : any = '';
  id : any = 1;
  firstname : any = '';
  lastname : any = '';

  constructor(public navCtrl: NavController, public navParams: NavParams, private connect : Connect) {
     let i = this.navParams.get('player');
     this.id = i.id;
  }

  ionViewDidLoad() {
     let that=this;
        let url = 'zs_profileinfo.php?id='+this.id;
        this.connect.getList(url).subscribe(
         data => {
  console.log(data);
            that.name = data.name;
            that.bio = data.bio;
            that.img = data.img;
            that.favplayer = data.favplayer;
            that.favteam = data.favteam;
            that.awards = data.awards;
            that.position = data.position;
            that.dob = data.dob;
         },
         err => {
         }
        ); 
  }

}



