import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Connect } from '../../providers/connect';
import { NewPage } from '../new/new';
import { EditprofilePage } from '../editprofile/editprofile';

/**
 * Generated class for the MembershipPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-membership',
  templateUrl: 'membership.html',
})
export class MembershipPage {
  public players : any[];
  public myAc : any = '';

  constructor(public navCtrl: NavController, public navParams: NavParams, private connect : Connect) {
    this.players = [];
  }

  ionViewDidEnter() {
     this.myAc = window.localStorage.getItem('myac');
     if (!this.myAc) this.myAc='';
     let that=this;
        let url = 'zs_myplayers.php?id='+this.myAc;
        this.connect.getList(url).subscribe(
         data => {
            that.players = data.players;
         },
         err => {
         }
        ); 
  }

  newreg() {
    this.navCtrl.push(NewPage);
  }

 goPlayer(a) {
   this.navCtrl.push(EditprofilePage , {id : a});
 }

}



