import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Connect } from '../../providers/connect';
import { ProfilePage } from '../profile/profile';

/**
 * Generated class for the PlayersPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
@Component({
  selector: 'page-players',
  templateUrl: 'players.html',
})
export class PlayersPage {

 public players : any[];
 public srch : any = '';
  constructor(public navCtrl: NavController, public navParams: NavParams, private connect : Connect) {
    this.players = [];
    this.srch='';
  }

  ionViewDidLoad() {
     let that=this;
        let url = 'zs_players.php';
        this.connect.getList(url).subscribe(
         data => {
            that.players = data.players;
         },
         err => {
         }
        ); 

  }

 findPlayer() {
     let that=this;
        let url = 'zs_players.php?srch='+this.srch;
        this.connect.getList(url).subscribe(
         data => {
            that.players = data.players;
         },
         err => {
         }
        ); 
 }

 goPlayer(a)
 { 
    this.navCtrl.push(ProfilePage,{player: a});
 }
}




