import { Component } from '@angular/core';
import {  NavController, LoadingController, ToastController, NavParams } from 'ionic-angular';
import { Connect } from '../../providers/connect';

@Component({
  selector: 'page-new',
  templateUrl: 'new.html',
})
export class NewPage {

  public reg : any;
  public teams : any = [];

  constructor(public navCtrl: NavController, public navParams: NavParams, private connect : Connect, public loadingCtrl : LoadingController, public toastCtrl : ToastController) {
    this.reg = {
      id : '',
      firstname: '',
      middlename: '',
      lastname: '',
      phoneh:'',
      phonem:'',
      postcode:'',
      email: '',
      address : '',
      gender : 'M',
      dob: '',
      contact : 'K',
      cname : '',
      cadd : '',
      cphone : '',
      shirt : '',
      division : '0',
      medical : ''
    }
    this.teams=[];
  }

  ionViewDidLoad() {
       let that = this;
       let url = 'zs_teamlist.php';
       this.connect.getList(url).subscribe(
          data => {
             that.teams = data.teams;
console.log(that.teams);
          },
          err => {
            that.connect.logError(err)
          }
        );

   
  }

 private presentToast(text) {
    let toast = this.toastCtrl.create({
      message: text,
      duration: 2000,
      position: 'top'
    });
    toast.present();
  }

  doRegister()
  { let msg='';
    if (!this.reg.division) msg="Please choose a team";
    if ((this.reg.firstname.length<1) || (this.reg.lastname.length<1)) 
      msg="Please enter the player's name";

    if (msg.length) 
      this.presentToast(msg);
    else {
       let that = this;
       let loading = this.loadingCtrl.create({
         content: 'Please wait...'
       });
       loading.present();
       let url = 'zs_playerregister.php?me='+window.localStorage.getItem('myac')+'&reg='+JSON.stringify(this.reg);
console.log(url);

       this.connect.getList(url).subscribe(
          data => {
            loading.dismiss();
            if (data.success) {
              window.localStorage.setItem('newplayer',data.id);
              that.navCtrl.pop();
            }
          },
          err => {
            loading.dismiss();
            that.presentToast("Could not register player");
            that.connect.logError(err)
          }
        );

    }
  }

}



