import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';

import { Connect } from '../../providers/connect';
import { TeamplayersPage } from '../teamplayers/teamplayers';

/**
 * Generated class for the TeamsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-teams',
  templateUrl: 'teams.html',
})
export class TeamsPage {

 public teams : any[];

  constructor(public navCtrl: NavController, public navParams: NavParams, private connect : Connect) {
     this.teams = [];
  }

  ionViewDidLoad() {
        let that=this;
        let url = 'zs_teamlist.php';
        this.connect.getList(url).subscribe(
         data => {
            that.teams = data.teams;
         },
         err => {
         }
        ); 
  }

  chooseTeam(t) {
    this.navCtrl.push(TeamplayersPage, { t : t});
  }

}



