import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Connect } from '../../providers/connect';
import { ProfilePage } from '../profile/profile';

/**
 * Generated class for the TeamplayersPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-teamplayers',
  templateUrl: 'teamplayers.html',
})
export class TeamplayersPage {
  players : any = [];
  teamname : any;
  team : any = 0;
  mode : any  = 0;
  constructor(public navCtrl: NavController, public navParams: NavParams, private connect : Connect) {
    let tt = this.navParams.get('t');
    this.teamname = tt.name;
    this.team = tt.id;
  }

  ionViewDidLoad() {
     this.mode = 0;
     let that=this;
        let url = 'zs_teaminfo.php?id='+this.team;
        this.connect.getList(url).subscribe(
         data => {
            that.players = data.players;
         },
         err => {
         }
        ); 

  }
 
  gomode(a) {
    this.mode = a;
  }

  goPlayer(p) {
    this.navCtrl.push(ProfilePage,{player : p});
  }

}



